import React from 'react';
import { Route, Redirect } from "react-router-dom";

import {
  SignIn,
  SignUp,
  ForgotPassword,
} from '../screens';

function routes(props) {
    //console.log('PRIM: ', props);
    const parentPath = props.match.path.replace(/\/$/, ''); // remove '/'
    console.log('AUTH: ' + parentPath);
    return [
       { path: `${parentPath}/(|sign-in/)`, component: SignIn, exact: true },
       { path: `${parentPath}/sign-up/`, component: SignUp },
       { path: `${parentPath}/forgot-password/`, component: ForgotPassword },
//       { component: () => <Redirect to={{ pathname: `${parentPath}/`, search: `?notfound=${window.location.href}` }}/> }, // not found
    ];
}

function AuthNavigator(props) {
    return (
        <React.Fragment>
            {routes(props).map(function (item) {
                return (
                    <Route {...item} {...props} key={item.path} />
                );
            })}
        </React.Fragment>
    );
}

export default AuthNavigator;
