import React from 'react';
import { Route, Redirect } from "react-router-dom";

import {
  Home,
  Terms,
  Privacy
} from '../screens';

function routes(props) {
    //console.log('PRIM: ', props);
    const parentPath = props.match.path.replace(/\/$/, ''); // remove '/'
    console.log('PRIM: ' + parentPath);
    return [
       { path: `${parentPath}/(|home/)`, component: Home, exact: true },
       { path: `${parentPath}/terms/`, component: () => <div>Terms</div> },
       { path: `${parentPath}/privacy/`, component: Privacy },
//       { component: () => <Redirect to={{ pathname: `${parentPath}/`, search: `?notfound=${window.location.href}` }}/> }, // not found
    ];
}

function PrimaryNavigator(props) {
    return (
        <React.Fragment>
            {routes(props).map(function (item) {
                return (
                    <Route {...item} {...props} key={item.path} />
                );
            })}
        </React.Fragment>
    );
}

export default PrimaryNavigator;
