import React from 'react';
import { HashRouter as Router, Switch, Route, Redirect } from "react-router-dom";

import PrimaryNavigator from './primary-navigator';
import AuthNavigator from './auth-navigator';

function routes(props) {
    const parentPath = props.match ? props.match.path : '/';
    console.log('ROOT: ' + parentPath);
    return [
       { path: `/`, component: PrimaryNavigator, exact: true },
       { path: `/auth/`, component: AuthNavigator },
       { component: () => <Redirect to={{ pathname: '/', search: `?notfound=${window.location.hash}` }}/> }, // not found
    ];
}

function RootNavigator(props) {
    return (
        <Router>
            <Switch>
                {routes(props).map(function(item) {
                    return (<Route {...item} key={item.path || 'notfound'} />);
                })}
            </Switch>
        </Router>
    )
}

export default RootNavigator;
